/** 
 * DOMContentLoaded:
 * 1DOMContentLoadedDOMContentLoaded
 * 2IE6IE7DOMContentLoadedonreadystatechange
 * 3) iedoScroll document.documentElement.doScroll("left");
      DOM doScrollDOMDOMContentLoaded
 */
export function domContentLoaded(fn) {
  const handler = fn
  if (document.addEventListener) {
    document.addEventListener('DOMContentLoaded', function () {
      document.removeEventListener('DOMContentLoaded', handler, false)
      fn()
    }, false)
  } else if (document.attachEvent) { // IE
    // iframe
    document.attachEvent('onreadystatechange', function () {
      if (document.readyState === 'completed') {
        document.detachEvent('onreadystatechange', handler)
        fn()
      }
    })

    // IEiframedoScroll DOM
    if (document.documentElement.doScroll && typeof window.frameElement === 'undefined') {
      try {
        document.documentElement.doScroll('left')
      } catch (e) {
        return setTimeout(handler, 20)
      }

      fn()
    }
  }
}



