import { ReportProvider } from '@/provider/report-provider'
import { domContentLoaded } from './dom-content-loaded'

~function (){
  //  HTML DOMContentLoaded 
  domContentLoaded(() => {
    const totalHeight = window.pageYOffset || document.documentElement.scrollTop // 
    const viewHeight = document.documentElement.clientHeight                     // 

    const imgs = Array.from(document.querySelectorAll('img')).map(img => {       // 
      const offsetTop = img.getBoundingClientRect().top + viewHeight // 
      return offsetTop < totalHeight ? img : null
    })

    const imgsLoadedTime = []                                                    // 
    let endTime = + new Date                                                     // 
    if (imgs.length > 0) {
      imgs.forEach(img => {
        if (!img || img.complete) {
          return
        }

        const image = new Image()
        image.src = img.src
        image.onload = () => imgsLoadedTime.push(+ new Date)
        image.onerror = () => imgsLoadedTime.push(+ new Date)
      })
    } else {
      endTime = + new Date
    }

    // load  
    window.onload = () => {
      if (imgsLoadedTime.length > 0) {
        endTime = Math.max.apply(null, imgsLoadedTime)
      }
      const firstScreenTime = endTime - loadStartTime
      console.log(': ', firstScreenTime)
      const allLoadedTime = + new Date() - loadStartTime

      ReportProvider.reportFePerformence({
        firstScreenTime,
        allLoadedTime,
        appName: 'Elapse-Admin',
      }).then()
        .catch(e => {
          console.log('report fe performence failed')
        })
    }
  })
}()



